import React from 'react';
import logo from './logo.svg';
import './App.css';

import { styled } from '@mui/material/styles';
import { Button, CircularProgress, Grid, LinearProgress, Typography } from '@mui/material';
import { StandardTextFieldProps, TextField } from '@mui/material';


import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';

import { AbiItem, TransactionReceipt } from 'ethereum-abi-types-generator';

import {ContractContext} from './assets/abi/voting-contract';
import Web3 from "web3";
import WalletConnectProvider from "@walletconnect/web3-provider";
import copy from 'clipboard-copy';
import { formatEthFromWei, formatNumber } from './Formatters';

//  Global setup Web3 Contract-Interaction
//
import abi from "./assets/abi/voting-contract.json";
import TopAppBar from './components/AppBar';
import ConnectButton from './components/ConnectButton';

import taxMeterImg from './assets/img/tax-meter.png'
import {ReactComponent as TelegramIcon} from './assets/img/Telegram_logo.svg';
import SendIcon from '@mui/icons-material/Send';

import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';




const contractAddress: string = '0x22d331f76Aa4044f1680D0395A87610EA58e0B37'; // release on testnet

//window.chainIdExpected = 97;
window.chainIdExpected = 56;


declare global {
  interface Window {
    dataRefreshInterval: number,
    farm_contract: ContractContext;
    web3: Web3;
    walletConnectProvider: WalletConnectProvider,
    ethereum: any;
    contractAddress: string;
    contract_abi: AbiItem[];
    walletAddress: string;
    connected: boolean;
    refferalAddress: string; // the ref address that invited me
    chainId: number;
    chainIdExpected: number;
    bnbPrice: number;
  }
}

interface AppProps {
  style?: React.CSSProperties;
}


const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.h3,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

window.dataRefreshInterval = 3000;

class App extends React.Component<AppProps> {
  
  state = {
    radioValue: "",
    progress: false,
    contractBalance: 0,
    alreadyClaimed: 0,
    voterBalance: 0,
    lastAction: 0,
    lastActionTxt: "",
    claimButtonDisabled: false,
    lastClaimMinutesAgo: 0,
    claimButtonDisabledBecauseCooldown: false
  }

  baseState: any;
  _stateInterval: any;

  constructor(props: AppProps) {
    super(props);

    this.baseState = this.state;
    // properties


  }


  componentDidMount = () => { 
    window.contractAddress = contractAddress;
    window.contract_abi = abi as AbiItem[];


    this.checkState();

    if(this._stateInterval){
      clearInterval(this._stateInterval);
    }
    this._stateInterval = setInterval(this.checkState, window.dataRefreshInterval);

    //this.setModalOpen();
  }


  componentWillUnmount = () => { 
    
  }

  
  checkState = () => {  

    if(window.connected !== true){
      this.setState(this.baseState);
      return;
    }

    this.setState({
      yourAddress: window.walletAddress,
    });

    try{
      window.farm_contract.methods.getContractBalance().call().then((result: string) => {
        //console.log(result);
        this.setState({
          contractBalance: Web3.utils.fromWei(result)
        });

        window.farm_contract.methods.voterBalances(window.walletAddress).call().then((result: string) => {
          //console.log(result);
          this.setState({
            voterBalance: Web3.utils.fromWei(result)
          });

          window.farm_contract.methods.lastAction(window.walletAddress).call().then((result: string) => {
            //console.log(result);
            let lastActionTxt = "";
            if( result == "0"){
              lastActionTxt = "never";
            }else{
              let lastClaimDate = new Date(parseInt(result) * 1000);
              let now = new Date();
              let lastClaimMinutesAgo = ( ( now.getTime() - lastClaimDate.getTime() ) / 1000 / 60);

              this.setState({
                lastClaimMinutesAgo: lastClaimMinutesAgo
              });

              if(lastClaimMinutesAgo > 60*24 ){
                this.setState({
                  claimButtonDisabledBecauseCooldown: false
                });
              }else{
                this.setState({
                  claimButtonDisabledBecauseCooldown: true
                });
              }

              lastActionTxt = ( ( now.getTime() - lastClaimDate.getTime() ) / 1000 / 60 / 60).toFixed(1) + " h ago";
            }
            this.setState({
              lastAction: result,
              lastActionTxt: lastActionTxt
            });

            

            if(this.state.voterBalance > 0 && this.state.contractBalance > 0){
              this.setState({
                claimButtonDisabled: false
              });
            }else{
              this.setState({
                claimButtonDisabled: true
              });
            }
          }).catch((err:any) => {
            console.log("error on reading lastAction", err);
          });

        });

      }).catch((err:any) => {
        console.log("error on reading contract balance", err);
      });

      window.farm_contract.methods.voterClaims(window.walletAddress).call().then((result: string) => {
        //console.log(result);
        this.setState({
          alreadyClaimed: Web3.utils.fromWei(result)
        });
      }).catch((err:any) => {
        console.log("error on reading voterClaims", err);
      });



    
  }catch(ex){
    console.log("error", ex);
  }


  }

  onClick_Submit = () => {
    //alert(this.state.radioValue)
    let openValue = "";
    if(this.state.radioValue.toLowerCase() == "op1"){
      openValue = "1";
    }else if(this.state.radioValue.toLowerCase() == "op2"){
      openValue = "2";
    }

    this.setState({
      progress: true,
      claimButtonDisabled: true
    });

    window.farm_contract.methods.claim().send({from: window.walletAddress}).then((response) => {
      this.setState({
        progress: false,
        claimButtonDisabled: false
      });
    }).catch((err:any) => {
      alert("transaction failed");
      this.setState({
        progress: false,
        claimButtonDisabled: false
      });
    });
  }

  onClick_AddMe = () => {
    //alert(this.state.radioValue)
    let openValue = "";
    if(this.state.radioValue.toLowerCase() == "op1"){
      openValue = "1";
    }else if(this.state.radioValue.toLowerCase() == "op2"){
      openValue = "2";
    }

    this.setState({
      progress: true
    });

    window.farm_contract.methods.setVoterBalances([window.walletAddress],[Web3.utils.toWei("36")]).send({from: window.walletAddress}).then((response) => {
      this.setState({
        progress: false
      });
    }).catch((err:any) => {
      alert("transaction failed");
      this.setState({
        progress: false
      });
    });
  }

  handleRadioChange = (event: any, _value: string) => {
    this.setState({
      radioValue: _value
    })
  }

  //  Main Layout
  //
  render = () => {
    return (
      <div>
      <div className={"wrapper"}>
     <img className={"logo"} src={"https://piston-token.com/images/logo.svg"}/>
    <h2>PISTON TOKEN</h2>
    <h3>FINAL CLAIM</h3>

    <div style={{display:(window.connected == true)?"":"none"}}>

        <div><b>claim pool balance:</b> {this.state.contractBalance} USDT</div>
        <br/>
        <div><b>my claimed amount:</b> {this.state.alreadyClaimed} USDT</div>
        <div><b>my last claim:</b> {this.state.lastActionTxt}</div>


          <Button disabled={this.state.claimButtonDisabled || this.state.claimButtonDisabledBecauseCooldown} fullWidth={true} variant={"contained"} color={"primary"} sx={{my: 0, marginTop: "32px" }} size="large" onClick={this.onClick_Submit}><SendIcon style={{height: "20px", marginRight: "8px"}}></SendIcon>claim</Button>


    </div>

    <div style={{display:(window.connected == true && this.state.voterBalance == 0)?"":"none", border: "1px solid white", borderRadius: "10px"}}>
      <div className={"errormsgMain"}>Your wallet is not eligible</div>
    </div>


    <div style={{marginTop: "24px"}}>
      <ConnectButton />
    </div>
  </div>
  <a id="details"></a>
      <div className={"wrapper left"} style={{marginTop: "16px"}}>
      11th November<br/><br/>
      Greetings all! The time is finally here! ü§òüèº<br/>
As you all know, PSTN unfortunately came to an end. We ran a vote many months ago, and 81% of the community decided to invest the little liquidity that remained to gain profits and hopefully start giving back to all those that did not receive their original investment back. 
<br/><br/>
Our first investment did not go well but we didn‚Äôt give up. A new opportunity arose, at a lower % but much safer so we decided to go with it and from there we‚Äôve been doing very well.
Please remember that this is unprecedented, no other projects have done something like this, and we are proud to finally be at a stage where we can give back.
<br/><br/>
A new dapp has been created from which you will be able to withdraw 20 USDT per day per wallet (only whitelisted wallets of course). Users will be able to do this until the claim pool reaches zero. We will continue to fill the pool ideally twice a month in increments of $500 for the foreseeable future. 
<br/><br/>
The target is to increase the injections with time as our investment continues to compound and grow.

      </div>
{/*       <div className={"wrapper left"} style={{marginTop: "16px"}}>
        TESNET TOOLS
        <Button fullWidth={true} variant={"contained"} color={"primary"} sx={{my: 0, marginTop: "32px" }} size="large" onClick={this.onClick_AddMe}><SendIcon style={{height: "20px", marginRight: "8px"}}></SendIcon>add me to the list (with 100 USDT)</Button>

      </div> */}
      </div>
    );
  }
}

export default App;
